<template>
  <div class="record">
    <div class="record-box">
      <div class="record-nav clearfix">
        <ul class="rnav-box fl" @click="navbtnEvent" :class="{active:navbtn}">
          <li class="rnav-title"><span></span></li>
          <li class="rnav-Str"></li>
        </ul>
        <ul class="rnav-box fl" @click="navbtnEvent" :class="{active:!navbtn}">
          <li class="rnav-title"></li>
          <li class="rnav-Str"></li>
        </ul>
      </div>
      <ul class="record-list">
        <li class="record-item" v-for="item in list" v-if="(navbtn?item.isShow:!item.isShow)">
          <ul class="record-top clearfix">
            <li class="record-left fl">
              <ul>
                <li class="record-flag" :class="RecordFlag(item.flag)">
                  <span class="flag-title">{{item.Title}}</span>
                </li>
                <li class="record-num">
                  <span class="record-img"><img src="/src/assets/images/icon/rmb1.png" alt=""></span>
                  <span class="record-text">{{item.number}}</span>
                </li>
              </ul>
            </li>
            <li class="record-right fr">
              <span class="record-cord">{{item.name}}</span>
              <span class="record-borrow">{{item.start}}</span>
              <span class="record-repay">{{item.end}}</span>
            </li>
          </ul>
          <ul class="record-bottom clearfix">
            <li class="record-left fl">
              <span>{{item.date}}</span>
              <span>(){{item.money}}</span>
            </li>
            <li class="record-right fr">
              <input type="button" v-if="item.isShow" class="btn" value="">
            </li>
          </ul>
        </li>
      </ul>
    </div>
  </div>
</template>
<script>
import Vue from 'vue';
// import Sign from './../../element/sign/sign';
import './../../../assets/css/main/index.css';
export default {
  name:'record',
  components: {

  },
  data () {
    return {
      navbtn :true,
      FlagTitle:'',
      Flag:'',
      list:[
        {flag:'start',isShow:true,Title:'',number:'5000',name:'(3022)',start:'2017.12.12',end:'2018.12.12',date:'21',money:'800.12'},
        {flag:'course',isShow:true,Title:'',number:'5000',name:'(3022)',start:'2017.12.12',end:'2018.12.12',date:'21',money:'800.12'},
        {flag:'overdue',isShow:true,Title:'',number:'5000',name:'(3022)',start:'2017.12.12',end:'2018.12.12',date:'21',money:'800.12'},
        {flag:'finish',isShow:false,Title:'',number:'5000',name:'(3022)',start:'2017.12.12',end:'2018.12.12',date:'21',money:'800.12'},
        {flag:'efficacy',isShow:false,Title:'',number:'5000',name:'(3022)',start:'2017.12.12',end:'2018.12.12',date:'21',money:'800.12'},

      ]
    }
  },
  methods: {
    // 
    navbtnEvent(){
      this.navbtn = !this.navbtn;
    },
    // 
    RecordFlag(Flag){
      switch(Flag){
        case 'start':
          this.FlagTitle ='';
          return 'start';
        case 'course':
          this.FlagTitle ='';
          return 'course';
        case 'overdue':
          this.FlagTitle ='';
          return 'overdue';
        case 'finish':
          this.FlagTitle ='';
          return 'finish';
        case 'efficacy':
          this.FlagTitle ='';
          return 'efficacy';
      }
    }
  }
}
</script>
<style scoped>

</style>



