<template>
  <div>
    <!--  -->
    <div class="sign" v-if="SignFlag">
      <div class="popup-box">
        <ul class="clearfix header">
          <li class="fl sign-name">
            <span></span>
          </li>
          <li class="fr" @click="CloseBtn">
            <div class="sign-close">
              <img src="/src/assets/images/icon/X.png" alt="">
            </div>
          </li>
        </ul>
        <ul class="sign-number">
          <li class="item-num">
            <input type="number" placeholder="">
          </li>
          <li class="item-code clearfix">
            <input class="fl" type="number" placeholder="">
            <input class="fr code-btn" @click="CodeBtn" :class="{active:CodeColor}" type="button" :value="CodeText">
          </li>
          <li class="item-pass">
            <input type="password" placeholder="(6-16)">
          </li>
        </ul>
        <div class="sign-protocol">
          <div class="sign-box clearfix">
            <div class="fl sign-select" @click="DealFun">
              <img :src="Deal?select1:select2" alt="">
            </div>
            <span class="fl"></span>
            <span class="fl"><router-link class="sign-deal" :to="{name:'protocol'}"></router-link></span>
          </div>
        </div>
        <div class="sign-btn"><input class="btn" type="button"  @click="signLogin" value=""></div>
        <div class="sign-register clearfix">
          <span class="fr sign-login" @click="GoLogin"></span>
        </div>
      </div>
    </div>
    <!--  -->
    <div class="login" v-if="isLogin">
      <div class="popup-box">
        <ul class="clearfix header">
          <li class="fl login-name">
            <span></span>
          </li>
          <li class="fr" @click="CloseLogin">
            <div class="login-close">
              <img src="/src/assets/images/icon/X.png" alt="">
            </div>
          </li>
        </ul>
        <ul class="login-number">
          <li class="item-num"><input type="number" placeholder=""></li>
          <li class="item-pass"><input type="password" placeholder=""></li>
        </ul>
        <div class="login-protocol">
          <div class="login-box clearfix">
            <div class="fl login-select" @click="DealFun">
              <img :src="Deal?select1:select2" alt="">
            </div>
            <span class="fl"></span>
          </div>
        </div>
        <div class="login-btn"><input class="btn" type="button" value="" @click="LoginFun"></div>
        <div class="login-register clearfix">
          <span class="fr login-password">
            <router-link :to="{name:'ResetLogin'}"></router-link>
          </span>
        </div>
      </div>
    </div>
    <!--  -->
    <div class="hint" v-if="HintFlag">
      <div class="popup-box">
        <ul class="clearfix header">
          <li class="fl hint-name"></li>
          <li class="fr">
            <div class="hint-close" @click="CloseSuc">
              <img src="/src/assets/images/icon/X.png" alt="">
            </div>
          </li>
        </ul>
        <ul class="hint-money">
          <li class="hint-title"><span></span></li>
          <li class="hint-num"><span class="flag"><img src="./../../../assets/images/icon/rmb1.png" alt=""></span><span>{{money}}</span></li>
        </ul>
        <div class="hint-btn"><input class="btn" @click="Succeed" type="button" value=""></div>
      </div>
    </div>
    <!--  -->
    <div class="defeated" v-if="isDefeated">
      <div class="popup-box">
        <ul class="clearfix header">
          <li class="fl defeated-name"></li>
          <li class="fr">
            <div class="defeated-close">
              <img src="/src/assets/images/icon/X.png" alt="">
            </div>
          </li>
        </ul>
        <ul class="defeated-box">
          <li class="defeated-img"><img src="/src/assets/images/icon/deleted.png" alt=""></li>
          <li class="defeated-title"><span v-text="update?'':''"></span></li>
        </ul>
        <div class="defeated-btn"><input class="btn" type="button" :value="update?'':''"></div>
      </div>
    </div>
  </div>
</template>
<script>
import Vue from 'vue';
import './../../../assets/css/main/index.css';
import select1 from './../../../assets/images/icon/select1.png'
import select2 from './../../../assets/images/icon/select2.png'

export default {
  name:'Sign',
  props:{
    SignFlag:{
      type:Boolean,
    },
    HintFlag:{
      type:Boolean,
    }
  },
  data () {
    return {
      CodeColor:false,//
      Deal:false,//
      select1:select1,
      select2:select2,
      isLogin:false,//
      isDefeated:false,//  
      update:false,// 
      CodeText:'',
      Num : 60,
      money:4800,
    }
  },
  methods: {
    // 
    signLogin:function(){
      this.CloseBtn(true);
    },
    // 
    LoginFun:function(){
      this.isLogin = !this.isLogin;
      this.CloseBtn(true);
    },
    // 
    CloseLogin:function(){
      this.isLogin = !this.isLogin;
      this.CloseBtn(false);
    },
    // 
    GoLogin:function(){
      this.CloseBtn(false);
      this.isLogin = !this.isLogin;
    },
    // 
    CloseBtn:function(data){
      this.$emit('isSignFun',data)
    },
    // 
    CloseSuc:function(){
      this.$emit('isHintFun',false)
    },
    // ()
    Succeed:function(){
      this.$emit('isHintFun',this.money)
    },
    // 
    CodeBtn:function(){
      this.SetTime();
      this.CodeColor = true;
      console.log('',this.addNumber(4))
    },
    // 4
    addNumber:function (_idx){
      var str = '';
      for(var i = 0; i < _idx; i += 1){
      str += Math.floor(Math.random() * 10);
      }
      return str;
    },
    // 
    SetTime:function(){
      let self = this;
      if(this.Num <= 0){
        this.CodeText = '';
        this.Num = 60;
        this.CodeColor = false;
      }else{
        this.CodeText = '('+this.Num+'s)';
        this.Num--;
        setTimeout(function(){
        self.SetTime();
        },1000)
      }
    },
    // 
    DealFun:function(){
      this.Deal = !this.Deal;
    }
  }
}
</script>
<style lang="stylus" scoped>
.item-code .code-btn{
  padding:0!important;
}
</style>




