        </li>
      </ul>
      <div class="bindCard-hint clearfix tl">
        <div class="fl hint-select">
          <img src="./../../../assets/images/icon/select1.png" alt="">
          <img src="./../../../assets/images/icon/select2.png" v-if="false" alt="">
        </div>
        <div class="fl hint-text">
          <span></span>
          <span class="more"></span>
          <span class="more"></span>
          <span class="more"></span>
        </div>
      </div>
      <input class="next-btn" @click="NextBtn" type="button" :value="BtnText">
    </div>
  </div>
</template>
<script>
import Vue from 'vue';
import './../../../assets/css/main/index.css';
export default {
  name:'LendMoney',
  data(){
    return{
      BtnText:'',
      MoneyNum:'4800',
      MoneyTime:'20',
      backCard:'',
      MoneySkill:'893.72',
      deduction:'',
      Refund:'4866.28',
      Limit:'5000',//
      IMgSrc:'select1.png',//
    }
  },
  methods: {
    NextBtn:function(){
    },
  }
}
</script>

<style scoped>
.pr40 {
    padding-right: 0.4rem;
}
.pr60 {
    padding-right: 0.6rem;
}
.bindCard-icon.Skill{
  width: 0.4rem!important;
}
</style>



