<template>
  <div class="homebox">
    <div class="ShowNum">
      <!--  -->
      <!-- 1   -->
      <ul class="ShowHint" v-if="state ===('0'||'1')">
        <li class="Money-hitbar">
          <span class="Money-hitText" v-show="HText">{{hitText}}</span>
        </li>
        <li class="Money-height"><span>(){{state}}</span></li>
        <li class="Money-num">
          <span class="rmbflag">
            <img src="/src/assets/images/icon/rmb1.png" alt="">
          </span>
          <span>{{MoneyNum}}</span>
        </li>
        <li class="Money-hit"><span> / </span></li>
        <li class="Money-btn">
          <button class="btn" @click="MoneyFun(state)">{{MoneyText}}</button>
        </li>
      </ul>
      <!--  -->
      <div class="Audit" v-if="state ===('2')">
        <div class="Audit-hitbar"><span class="Audit-hitText" v-show="HText">{{hitText}}</span></div>
        <div class="Audit-title"><span>{{state}}</span></div>
        <div class="Audit-time clearfix">
          <ul class="Audit-list fl">
            <li class="item-title"><span></span></li>
            <li class="item-tiem"><span>201828</span></li>
          </ul>
          <ul class="Audit-list fr">
            <li class="item-title"><span></span></li>
            <li class="item-tiem"><span>21</span></li>
          </ul>
        </div>
        <ul class="Audit-btn">
          <!-- 2  3 3 -->
          <li v-if="state ===('2'||'3')">
            <input type="button" class="btn-info" @click="MoneyFun(state)" v-text="MoneyText">
          </li>
          <!-- 3 -->
          <li  v-if="state ===('4')">
            <input type="button" class="btn-refund" value="">
            <input type="button" class="btn-renew" value="">
          </li>
        </ul>
      </div>
      <!--  -->
      <ul class="MoneyBox">
        <li class="Moneyitem">
          <ul>
            <li class="MoneyImg"><img src="/src/assets/images/icon/jklc.png" alt=""></li>
            <li class="Moneyname1"><span></span></li>
            <li class="Moneyname2"><span></span></li>
          </ul>
        </li>
        <li class="Moneyitem">
          <ul>
            <li class="MoneyImg"><img src="/src/assets/images/icon/hk.png" alt=""></li>
            <li class="Moneyname1"><span></span></li>
            <li class="Moneyname2"><span></span></li>
          </ul>
        </li>
      </ul>
      <!--  -->
      <ul class="sharelist">
        <li class="shareItem">
          <ul class="register">
            <li class="share-hit1"><span></span></li>
            <li class="share-hit2"><span>300</span></li>
            <li class="share-btn"><input type="button" @click="SignFun" class="btn" value=""></li>
          </ul>
        </li>
        <li class="shareItem">
          <div class="jb">
            <img src="/src/assets/images/icon/jb.png" alt="">
          </div>
        </li>
      </ul>
    </div>
    <v-Sign :SignFlag="isSign" @isSignFun = "SignEvent"></v-Sign>
    <v-hint :HintFlag="ishint" @isHintFun = "HintEvent"></v-hint>
  </div>
</template>

<script>
import Vue from 'vue';
import Sign from './../../element/sign/sign';
import Hint from './../../element/sign/sign';
import './../../../assets/css/main/index.css';
export default {
  name: 'home',
  components: {
    'v-Sign':Sign,
    'v-hint':Hint,
  },
  data () {
    return {
      isSign:false,//
      ishint:false,//
      MoneyText:'',//btntext
      state:'0',//
      MoneyNum:'5000',//money number
      HText:false,// 
      hitText:'',//
    }
  },
  created () {
    var self = this;
    setTimeout(function(){
      self.MoneyTime(0,self.MoneyNum)
    },1)
  },
  methods: {
    // 
    SignFun:function(){
      this.isSign = true;
    },
    //  
    SignEvent:function(data){
      this.isSign = false;
      if(data){
        this.state = '1';
      }
    },
    //
    HintSuc:function(){
      this.ishint = true;
      this.MoneyText='';
    },
    // 
    HintEvent:function(data){
      this.ishint = false;
      var self = this;
      if(data){
        this.state = '2';
        this.MoneyNum = data;
        this.MoneyText = '';
        this.HText=true,// 
        this.hitText= ''+ this.MoneyNum +'',//
        setTimeout(function(){
          self.MoneyTime(0,self.MoneyNum)
        },1)
      }
    },
    // 
    MoneyFun:function(state){
      switch(state){
        case'0':
          alert('')
          return;
        case '1':   //
          this.MoneyText=',';
          this.SetTime(3)
          return;
        case '2':  //
          this.$router.push({name: 'seting'});
          return;
      }
    },
    // 
    SetTime:function(Num){
      let self = this;
      if(/^[0-9]+.?[0-9]*$/.test(Num)){
        var Time = '';
        self.Time = Num;
      }
      if(self.Time <= 0){
        self.Time = 3;
        this.HintSuc()
      }else{
        self.Time--;
        setTimeout(function(){
        self.SetTime();
        },1000)
      }
    },
    // 
    MoneyTime:function(num,Aim){
      var self = this;
      num = num + 5;
      self.MoneyNum = num;
      if(num < Aim){
        setTimeout(function(){
          self.MoneyTime(num,Aim)
        },1)
      }
    },
  }
}
</script>

<style scoped lang="stylus">

</style>



