<template>
  <div class="binding">
    <div class="binding-box">
      <ul class="binding-list">
        <li class="binding-item clearfix">
          <span class="fl tl binding-label"></span>
          <input class="fl binding-text" placeholder="" type="text">
        </li>
        <li class="binding-item clearfix" v-if="false">
          <span class="fl tl binding-label"></span>
          <input class="fl binding-text"  placeholder="" type="number">
        </li>
        <router-link style="display:block" :to="{name:'bindCard'}">
          <li class="binding-item clearfix relative">
            <span class="fl tl binding-label"></span>
            <input class="fl binding-text" placeholder="" type="number">
            <div class="binding-icon absolute">
              <img src="./../../../assets/images/icon/right.png" alt="">
            </div>
          </li>
        </router-link>
      </ul>
      <div class="binding-hint">
        <span></span>
      </div>
      <input class="next-btn active" @click="NextBtn" type="button" :value="BtnText">
      <span class="binding-more"></span>
    </div>
  </div>
</template>
<script>
import Vue from 'vue';
import './../../../assets/css/main/index.css';
export default {
  name:'binding',
  data(){
    return{
      isNext:false,//
      BtnText:'',
      Password:'',
    }
  },
  methods: {
    NextBtn:function(){
      this.BtnText = '';
      console.log(this.Password);
      this.Password= '';
    },
  }
}
</script>

<style scoped>

</style>



