import Vue from 'vue'
import Router from 'vue-router'
import axios from 'axios'

// 
require('@/assets/css/public/animate.css')
require('@/assets/css/public/public.css')
    // require('@/assets/css/main/index.css')

//
import mobile from '@/components/mobile/mobile.vue'
import navbar from '@/components/element/navbar.vue'
import home from '@/components/template/home/home.vue' //
import record from '@/components/template/record/record.vue' //
import question from '@/components/template/question/question.vue' //
import AboutUs from '@/components/template/AboutUs/AboutUs.vue' //
import idea from '@/components/template/idea/idea.vue' //
import seting from '@/components/template/seting/seting.vue' //
import resume from '@/components/template/resume/resume.vue' //
import nickname from '@/components/template/nickname/nickname.vue' //
import identity from '@/components/template/password/identity.vue' //
import note from '@/components/template/password/note.vue' //
import ResetPass from '@/components/template/password/ResetPass.vue' //
import ResetLogin from '@/components/template/password/ResetLogin.vue' //
import setPass from '@/components/template/password/setPass.vue' //
import bankCard from '@/components/template/bankCard/bankCard.vue' //
import binding from '@/components/template/bankCard/binding.vue' //
import bank from '@/components/template/bankCard/bank.vue' //
import bindCard from '@/components/template/bankCard/bindCard.vue' //
import LendMoney from '@/components/template/LendMoney/LendMoney.vue' //
import discount from '@/components/template/discount/discount.vue' //
import coupon from '@/components/template/discount/coupon.vue' //
import myinfo from '@/components/template/myinfo/myinfo.vue' //
import wallet from '@/components/template/myinfo/wallet.vue' //
import moneyInfo from '@/components/template/moneyInfo/moneyInfo.vue' //
import bargain from '@/components/template/moneyInfo/bargain.vue' //
import discover from '@/components/template/discover/discover.vue' //
import protocol from '@/components/template/protocol/protocol.vue' //
import message from '@/components/template/protocol/message.vue' //
import messagebox from '@/components/template/protocol/messagebox.vue' //


Vue.use(Router)

export default new Router({
    routes: [
        // {
        //       path: '*',
        //       name: '/',
        //       redirect: '/'
        //   },
        {
            path: '/',
            name: 'mobile',
            redirect: '/mobile/home',
            component: mobile,
            children: [{
                path: '/mobile/home',
                name: 'home',
                component: home,
                meta: { navShow: true, cname: '', right: 'news' }
            }, {
                path: '/mobile/discover',
                name: 'discover',
                component: discover,
                meta: { navShow: true, cname: '', }
            }, {
                path: '/mobile/myinfo',
                name: 'myinfo',
                component: myinfo,
                meta: { navShow: true, cname: '', font: '#ffffff', bgcolor: '#26b0ff', right: 'seting' }
            }, {
                path: '/mobile/record',
                name: 'record',
                component: record,
                meta: { navShow: false, cname: '', left: 'back' }
            }, {
                path: '/mobile/coupon',
                name: 'coupon',
                component: coupon,
                meta: { navShow: false, cname: '', left: 'back', right: 'bargain', Text: '' }
            }, {
                path: '/mobile/bankCard',
                name: 'bankCard',
                component: bankCard,
                meta: { navShow: false, cname: '', left: 'back' }
            }, {
                path: '/mobile/wallet',
                name: 'wallet',
                component: wallet,
                meta: { navShow: false, cname: '', left: 'back1', font: '#ffffff', bgcolor: '#26b0ff', right: 'bargain', Text: '' }
            }, {
                path: '/mobile/question',
                name: 'question',
                component: question,
                meta: { navShow: false, cname: '', left: 'back' }
            }, {
                path: '/mobile/AboutUs',
                name: 'AboutUs',
                component: AboutUs,
                meta: { navShow: false, cname: '', left: 'back' }
            }, {
                path: '/mobile/idea',
                name: 'idea',
                component: idea,
                meta: { navShow: false, cname: '', left: 'back' }
            }, {
                path: '/mobile/seting',
                name: 'seting',
                component: seting,
                meta: { navShow: false, cname: '', left: 'back' }
            }, {
                path: '/mobile/resume',
                name: 'resume',
                component: resume,
                meta: { navShow: false, cname: '', left: 'back' }
            }, {
                path: '/mobile/nickname',
                name: 'nickname',
                component: nickname,
                meta: { navShow: false, cname: '', left: 'back' }
            }, {
                path: '/mobile/identity',
                name: 'identity',
                component: identity,
                meta: { navShow: false, cname: '', left: 'back' }
            }, {
                path: '/mobile/note',
                name: 'note',
                component: note,
                meta: { navShow: false, cname: '', left: 'back' }
            }, {
                path: '/mobile/ResetPass',
                name: 'ResetPass',
                component: ResetPass,
                meta: { navShow: false, cname: '', left: 'back' }
            }, {
                path: '/mobile/ResetLogin',
                name: 'ResetLogin',
                component: ResetLogin,
                meta: { navShow: false, cname: '', left: 'back' }
            }, {
                path: '/mobile/setPass',
                name: 'setPass',
                component: setPass,
                meta: { navShow: false, cname: '', left: 'back' }
            }, {
                path: '/mobile/binding',
                name: 'binding',
                component: binding,
                meta: { navShow: false, cname: '', left: 'back' }
            }, {
                path: '/mobile/bindCard',
                name: 'bindCard',
                component: bindCard,
                meta: { navShow: false, cname: '', left: 'back' }
            }, {
                path: '/mobile/LendMoney',
                name: 'LendMoney',
                component: LendMoney,
                meta: { navShow: false, cname: '', left: 'back' }
            }, {
                path: '/mobile/discount',
                name: 'discount',
                component: discount,
                meta: { navShow: false, cname: '', left: 'back' }
            }, {
                path: '/mobile/moneyInfo',
                name: 'moneyInfo',
                component: moneyInfo,
                meta: { navShow: false, cname: '', font: '#ffffff', bgcolor: '#26b0ff', left: 'back1', right: 'bargain', Text: '' }
            }, {
                path: '/mobile/bargain',
                name: 'bargain',
                component: bargain,
                meta: { navShow: false, cname: '', left: 'back' }
            }, {
                path: '/mobile/bank',
                name: 'bank',
                component: bank,
                meta: { navShow: false, cname: '', left: 'back' }
            }, {
                path: '/mobile/protocol',
                name: 'protocol',
                component: protocol,
                meta: { navShow: false, cname: '', left: 'back' }
            }, {
                path: '/mobile/message',
                name: 'message',
                component: message,
                meta: { navShow: false, cname: '', left: 'back' }
            }, {
                path: '/mobile/messagebox',
                name: 'messagebox',
                component: messagebox,
                meta: { navShow: false, cname: '', left: 'back' }
            }]
        }
    ]
})


